:host ::ng-deep {
  .crop-container {
    height: 400px;
    width: 100%;
    position: relative;
  }
  .loading-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  .upload-zone {
    min-height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .upload-component {
    width: 100%;
  }
  .p-fileupload-content {
    min-height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .preview-section {
    height: 100%;
    padding: 1rem;
  }
  .preview-container {
    background-color: white;
    min-height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .image-editor-dialog {
    width: 1000px;
  }

  .upload-replace-component {
    .p-fileupload {
      border: none;
      background: none;
      padding: 0;
    }

    .p-fileupload-header {
      padding: 0;
      border: none;
      background: none;
    }
  }

  .upload-different-btn {
    &:hover {
      transform: translateY(-1px);
    }
  }

  .hidden-file-input {
    display: none;
  }
}
